.App, .HorizontalFlexTitle, .HorizontalFlexContent {
  display: flex;
}

.App {
  max-width: 1000px;
  flex-direction: column;
  font-family:Arial,Helvetica,sans-serif;
}

.Title {
  font-size: xx-large;
  padding: 0px 20px 10px 5px;
}

.TitleHR {
  flex: 2 1 800px;
  background-color: black;
  width:100%;
  height: 1px;
  align-self: flex-end;
  margin-bottom: 20px;
}

.SubTitle {
  font-size:large;
  padding: 0px 20px 20px 5px;
}

.IntroText {
  font-size: small;
  padding: 0px 20px 20px 5px;
}

.AppLeftSide{
  flex: 0 1 275px;
  display: flex;
  flex-direction: column;
}

.AppRightSide{
  flex: 2 1 auto;
}

@media screen and (max-width: 600px) {
  .HorizontalFlexContent {
    flex-direction: column;
  }

  .TitleHR{
    flex: 1 1 auto;
  }

  .filterContainer {
    margin-bottom: 10px;
  }
  
}
